.icon_plus1 {
  width: 1.25vw;
  height: 1.25vw;
  position: relative;
}
.icon_plus1::before, .icon_plus1::after {
  content: "";
  background-color: #00ADC6;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 300ms linear;
}
.icon_plus1::before {
  width: 1px;
  height: 1vw;
}
.icon_plus1::after {
  width: 1vw;
  height: 1px;
}

