@use "../../../global"as g;

.icon {
    &_plus1 {
        width: g.vw(20);
        height: g.vw(20);

        position: relative;

        &::before,
        &::after {
            content: '';
            background-color: #00ADC6;
            position: absolute;
            top: 50%;
            left: 50%;

            transform: translate(-50%, -50%);
            transition: all 300ms linear;
        }

        &::before {
            width: 1px;
            height: g.vw(16);
        }

        &::after {
            width: g.vw(16);
            height: 1px;
        }
    }
}



.block_1 {
    margin-top: g.vw(101);

    .text_intro {
        @include g.fonts(0, 25, 700, 45, 0, #44474E);
        font-size: g.vw(25);
    }

    .list {
        width: g.vw(960);

        margin-top: g.vw(82);
        margin-left: auto;
        margin-right: auto;

        padding-top: g.vw(33);
        padding-left: g.vw(50);
        padding-right: g.vw(50);
        padding-bottom: g.vw(39);

        background-color: #FAFAFA;

        &_tag {
            @include g.fonts(0, 25, 700, 35, 0, #2C68C9);
            font-size: g.vw(25);
            position: relative;

            display: flex;
            align-items: center;

            .icon_plus {
                margin-left: auto;
            }

            img {
                display: inline-block;
                width: g.vw(20);
                margin-right: 6px;
            }

            &.show {
                .icon_plus {
                    &::before {
                        opacity: 0;
                    }
                }
            }
        }

        &_content {
            margin-top: g.vw(24);
        }

        &_item {
            border-top: 1px solid #E6EBEF;
            padding-left: g.vw(26);
            padding-top: 3px;
            padding-bottom: 2px;
        }

        .nav_link {
            width: 100%;
            display: inline-block;
            position: relative;
            @include g.fonts(0, 18, 700, 36, 0, #44474E);
            font-size: g.vw(18);
            padding-bottom: 3px;

            .icon_image {
                width: g.vw(20);
                height: g.vw(20);
                position: absolute;
                top: 50%;
                right: g.vw(30);
                transform: translateY(-50%);
            }
        }
    }
}

.mt {
    &_0 {
        margin-top: 0 !important;
    }

    &_30 {
        margin-top: g.vw(30) !important;
    }
}

.mb {
    &_14 {
        margin-bottom: g.vw(14) !important;
    }

    &_19 {
        margin-bottom: g.vw(19) !important;
    }

    &_29 {
        margin-bottom: g.vw(29) !important;
    }
}

.block {
    &_cmm {
        padding-top: g.vw(104);
        padding-bottom: g.vw(26);

        // &+.text_fluid {

        // }
    }

    &_bc {
        width: g.vw(1160);

        padding-left: g.vw(50);
        padding-right: g.vw(50);
        padding-top: g.vw(0);
        padding-bottom: g.vw(25);

        margin-left: auto;
        margin-right: auto;

        background-color: #F5FAFB;
    }

    &_title {
        margin-top: -1px;
        margin-left: -4px;
        transform: translateY(-50%);
    }
   
    .text_center {
        &+.block_lead {
            margin-top: g.vw(21);
        }

        &+.text_desc {
            margin-top: g.vw(15);
        }

        &+.text_with_dot {
            margin-top: g.vw(17);
        }

        &+.text_20 {
            margin-top: g.vw(16);
        }
    }

    &_lead {
        @include g.fonts(0, 25, 700, 35, 0, #2C68C9);
        font-size: g.vw(25);

        margin-bottom: g.vw(24);

        &_with_order {
            margin-bottom: g.vw(20);
            margin-top: g.vw(50);

            .order {
                width: g.vw(47);
                min-width: g.vw(47);
                font-family: g.$f_montserrat;
                font-weight: 500;

                display: inline-block;
            }

            &+.image_fluid {
                margin-top: g.vw(21);
                margin-bottom: g.vw(31);
            }
        }

        &+.text_desc {
            margin-top: g.vw(-7);
        }
    }

    .text_desc {
        margin-bottom: g.vw(20);

        &+.text_with_dot {
            margin-top: g.vw(44);
        }

        &+.block_lead {
            margin-top: g.vw(48);
        }
    }

    .text_20 {
        @include g.fonts(0, 20, 700, 35, 0, #44474E);
        font-size: g.vw(20);
    }

    .text_with_dot {
        display: flex;

        @include g.fonts(0, 20, 700, 32, 0, #44474E);
        font-size: g.vw(20);
        margin-bottom: g.vw(22);

        &::before {
            content: '';
            display: inline-block;
            min-width: 8px;
            width: 8px;
            height: 8px;

            background-color: #2C68C9;

            border-radius: 99px;

            margin-top: g.vw(12);
            margin-right: g.vw(20);

        }

        &+.text_desc {
            margin-top: g.vw(44);
        }

        &+.block_lead {
            margin-top: g.vw(50);
        }
    }
}

.block_blabla {
    margin-top: g.vw(200);
    padding-bottom: g.vw(100);

    &_container {
        width: g.vw(1180);

        margin-left: auto;
        margin-right: g.vw(100);

        background: rgb(102, 172, 192);
        background: linear-gradient(270deg, rgba(102, 172, 192, 1) 0%, rgba(44, 104, 201, 1) 100%);
        position: relative;
    }

    &_bi {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &_image {
        width: g.vw(666);
        height: auto;
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;

        transform: translate(-43%, -22%);
    }

    &_content {
        width: g.vw(634);

        margin-left: auto;
        margin-right: g.vw(150);

        padding-top: g.vw(120);
        padding-bottom: g.vw(95);

        position: relative;
        z-index: 2;

        .block_title {
            transform: none;
            margin-bottom: g.vw(48);
        }

    }

    .text_with_counter {
        position: relative;

        @include g.fonts(0, 20, 700, 38, 0, #FFF);
        font-size: g.vw(20);

        padding-left: g.vw(60);
        margin-bottom: g.vw(26);

        .counter {
            @include g.fonts(g.$f_montserrat, 30, 700, 60, 0, #FFF);
            font-size: g.vw(30);

            margin-right: g.vw(25);
            position: absolute;
            left: 0;
            top: g.vw(-11);
        }

        .underline {
            width: 100%;
            border-bottom: 1px solid rgba($color: #FFF, $alpha: .5);
            padding-bottom: g.vw(15);
            display: inline-block;
        }
    }
}

.legend {
    border: 1px solid #D1DAE6;
    padding-left: g.vw(50);
    padding-right: g.vw(62);
    padding-bottom: g.vw(20);

    margin-top: g.vw(67);
    margin-bottom: g.vw(20);

    position: relative;

    &_field_name {
        @include g.fonts(0, 25, 700, 35, 0, #2C68C9);
        font-size: g.vw(25);

        position: absolute;
        top: g.vw(-18);
        left: g.vw(18);

        background-color: #F5FAFB;
    }

    &_context {
        padding-top: g.vw(27);

        >.text_desc {
            margin-top: g.vw(14);
            margin-bottom: g.vw(27);
        }
    }

    &_text {
        padding-top: g.vw(27);
        padding-bottom: g.vw(15);

        @include g.fonts(0, 16, 700, 32, 0, #44474E);
        font-size: g.vw(16);

        &.text_dot {
            display: flex;

            &::before {
                content: '';
                display: block;
                min-width: 8px;
                width: 8px;
                height: 8px;

                background-color: #2C68C9;

                border-radius: 99px;

                margin-top: g.vw(10);
                margin-right: g.vw(9);
            }


        }

        &+.text_dot {
            border-top: 1px solid rgba($color: #8798BA, $alpha: .5);
        }
    }
}

.image_fluid {
    background-color: #fff;
    padding-top: g.vw(16);
    padding-bottom: g.vw(16);

    img {
        display: block;
        width: g.vw(860);
        height: auto;
        margin-left: auto;
        margin-right: auto;
    }
}

.text_fluid {
    width: 100%;
    height: g.vw(200);
    display: flex;
    align-items: center;
    justify-content: center;

    @include g.fonts(0, 35, 700, 70, 0, #FFF);
    font-size: g.vw(35);

    background-color: #1D4156;
    margin-top: g.vw(76);
    margin-bottom: g.vw(24);
}

